import React from 'react';
// import { Link } from 'react-router-dom';
import firebase from '../Firebase';
import Storeinfo from './Storeinfo';
import './Store.css';
import Logo from '../logo.svg';
import { FaRupeeSign } from 'react-icons/fa';


// offer price is MRP, selling price is discounted one
class App extends React.Component {
	constructor(props) {
		super(props);
		var id ='';
		//I know very worst logic below, but I'm short of time!!
		if(this.props.match.params.storeName === 'NavrangNX') {
			id = '0SA6w8WsXFwqeKbkrnQT';
		} else if(this.props.match.params.storeName === 'MananFashion'){
			id = '1DpFqPsCGcwDwsTD1s3u';
		} else if(this.props.match.params.storeName === 'ZZFashionPvtLtd'){
			id = 'CH4eo0tCW88PZ50gIhOM';
		} else if(this.props.match.params.storeName === 'AnandDeepFashion'){
			id = 'NjYjmkoXxyxheWGTkObG';
		} else if(this.props.match.params.storeName === 'PoojaCreation'){
			id = 'WiP2YC2QpT3DKETDukzA';
		} else if(this.props.match.params.storeName === 'SrinidhiSilks'){
			id = 'Z8hSLsade47vy1QHdhfH';
		} else if(this.props.match.params.storeName === 'Mokshii'){
			id = 'hqWeQYxI51X0WUKCHAqm';
		} else if(this.props.match.params.storeName === 'BandhanBoutique'){
			id = 'wRVtILQ9NIFcPEJO2Tl5';
		}
		this.ref = firebase.firestore().collection('products').where('merchantId', '==', id).limit(28);
		this.unsubscribe = null;
		this.state = { products: [], id };
	}

	onCollectionUpdate = (querySnapshot) => {
		const products = [];
		querySnapshot.forEach((doc) => {
			const { productImage, productName, productDetail, offerPrice, sellingPrice } = doc.data();
			products.push({
				key: doc.id,
				doc,
				productImage,
				productName,
				productDetail,
				offerPrice,
				sellingPrice
			});
		});
		this.setState({ products });
	}

	componentDidMount = () => {
		this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
	}
// https://play.google.com/store/apps/details?id=com.nearst
	getProducts = () => {
		const foundProducts = this.state.products.map((product) => {
			const percentage = (product.sellingPrice / product.offerPrice) * 10;
			const offvalue = Math.trunc(percentage);
			let appLink = '';
			if (window.screen.width <= 699) {
				appLink = `https://nearst.page.link/?link=https://nearstUser.com/openStore?route%3DstoreDetails%26id%3D${this.state.id}&apn=com.nearst&amv=2`

			} else {
				appLink = "https://play.google.com/store/apps/details?id=com.nearst";
			}
			return (
				<div className="col-lg-3 col-md-4 col-sm-6 col-xs-12" key={product.key}>
					<div className="card sareecard" style={{ minHeight: "415px" }}>
						<img src={product.productImage[0]} className="card-img-top embed-responsive-item " alt="saree" />
						<div className="card-body">
							<div className="card-title d-flex justify-content-between">
								<div><b><FaRupeeSign icon="rupee-sign" /> {product.sellingPrice}</b></div>
								<div><strike><FaRupeeSign icon="rupee-sign" /> {product.offerPrice}</strike></div>
								<div style={{ color: "red" }}>{offvalue}% off</div>
							</div>
							<div className="card-title" style={{ color: " rgb(235, 92, 122)" }}>{product.productName}</div>
							<p className="card-text">{product.productDetail.substr(0, 50)}</p>
							<a href={appLink} target="_blank" className="btn">View Product</a>
						</div>
					</div><br />
				</div>
			);
		});

		return foundProducts;
	}

	render = () => {
		return (
			<div className="container" style={{ paddingLeft: "7%", paddingRight: "7%" }}>
				<nav className="navbar navbar-expand-md navbar-light">
					<a className="navbar-brand" href="#"><img src={Logo} width="100" /></a>
				</nav>
				<Storeinfo id={this.state.id} /> <br /><br />
				<div className="row">
					{this.getProducts()}
				</div>
			</div>
		);
	}
}

export default App;