import React from 'react';
import { Link } from 'react-router-dom';
import firebase from './Firebase';
import Logo from "./logo.svg";
import 'bootstrap/dist/css/bootstrap.min.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection('merchant');
    this.unsubscribe = null;
    this.state = { merchants: [] };
  }

  onCollectionUpdate = (querySnapshot) => {
    const merchants = [];
    querySnapshot.forEach((doc) => {
      const { storeName, bannerImage, address, userPhone, openingTiming, closingTiming } = doc.data();
      merchants.push({
        key: doc.id,
        doc,
        storeName,
        bannerImage,
        address,
        userPhone,
        openingTiming,
        closingTiming
      });
    });
    this.setState({ merchants });
  }

  componentDidMount = () => {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  getMerchants = () => {
    const foundMerchants = this.state.merchants.map((merchant) => {
      let newStorName = merchant.storeName.replace(/\s/g, "");
      console.log(newStorName);
      return (
        <div className="card" key={merchant.key}>
          <div className="card-body">
            <Link to={`/${newStorName}`}><button style={{ margin: '3px' }} className="btn btn-primary">{merchant.storeName}</button></Link>
          </div>
        </div>
      );
    });
    return foundMerchants;
  }

  render = () => {
    return (
      <div className="container" >
        <nav className="navbar navbar-expand-md navbar-light">
          <a className="navbar-brand" href="#"><img src={Logo} width="100" /></a>
        </nav>
        <div className="row">
          {this.getMerchants()}
        </div>
      </div>
    );
  }
}

export default App;